  const arr = markerChars;
  const arrLength = arr.length;
  const temp = Array(length);
  (function fill(pos, start) {
    if (pos === length) return choices.push(temp.join(''));
    for (let i = start; i < arrLength; i++) {
      temp[pos] = arr[i];
      fill(pos + 1, i);
    }
  })(0, 0);
